export function unregisterRemind(data: any) {
  const obj = { ...(data || {}), log: undefined }
  const host = 'sawavideo'
  if (getArgsFromUrl('is_debug') === '2') {
    const link = `${host}://unregister_remind/?data=${encodeURIComponent(JSON.stringify(obj))}`
    console.warn('[unregister_remind]', link)
    return
  }
  router(`${host}://unregister_remind/?data=${encodeURIComponent(JSON.stringify(obj))}`)
}

export function refreshDeviceToken(
  mobile: number | null | string,
  country: string | null,
  flag: string | null
) {
  if (mobile) localStorage.setItem('ludo_refresh_device_token_mobile', `${mobile}` || '')
  if (country) localStorage.setItem('ludo_refresh_device_token_country', `${country}` || '')
  if (flag) localStorage.setItem('ludo_refresh_device_token_flag', `${flag}` || '')

  const host = 'sawavideo'
  let href = location.href
  href = setUrlParams2('is_refresh', 1)
  href = `${host}://refresh_device_token/?url=${encodeURIComponent(href)}`
  console.warn('refreshDeviceToken', href)
  router(href)
}

export function getSub(access_token: string) {
  const arr = access_token.split('.')
  if (arr.length < 3) {
    return ''
  }
  try {
    const obj = arr[1]
    const subs = atob(obj)
    const o = JSON.parse(subs)
    const sub = o.sub
    return sub
  } catch (e) {
    return ''
  }
}

export function showBanToast(d: string) {
  const dd = Number(d)
  if (dd) {
    if (dd === -1) {
      showToast(_T('因涉嫌违规，你的账号被永久封禁。你无法再登录该账号。'))
    } else {
      const target = dayjs(dd * 1000)
        .utcOffset(3)
        .format('YYYY-MM-DD')
      showToast(
        sprintf(_T('因涉嫌违规，你的账号被封禁至{date}，这段时间内你将无法登陆该账号。'), {
          date: target,
        })
      )
    }
  }
}

export function versionToNumber(versionString: string) {
  const parts = versionString.split('.')

  // 确保至少有三位
  while (parts.length < 3) {
    parts.push('0')
  }

  let result = ''
  for (let i = 0; i < 3; i += 1) {
    let currentPart = parts[i]

    if (i > 0 && currentPart.length < 2) {
      currentPart = currentPart.padStart(2, '0')
    }

    result += currentPart
  }

  return parseInt(result, 10)
}

export function getAppClientVersion() {
  const version_code = getArgsFromUrl('version_code')
  if (!version_code) return ''
  // 处理参数有多个 version_code 的情况
  let vc = version_code
  if (Object.prototype.toString.call(version_code) === `[object ${'Array'}]`) {
    vc = version_code[0]
  }
  return versionToNumber(vc).toString()
}

/// darkmode
// dark mode
function applyColorTheme(darkMode: boolean) {
  if (darkMode) {
    document.getElementsByTagName('html')[0].classList.add('-dark')
  } else {
    document.getElementsByTagName('html')[0].classList.remove('-dark')
  }
}

function isSafariDarkModeSupport() {
  const mode = getArgsFromUrl('mode')
  if (mode) {
    return true
  }
  return false
}

function enableDarkModeiOS() {
  const darkModeMediaQuery = window.matchMedia('(prefers-color-scheme: dark)')
  darkModeMediaQuery.addEventListener('change', (e) => {
    if (e.matches) {
      applyColorTheme(true)
    } else {
      applyColorTheme(false)
    }
  })
  if (darkModeMediaQuery.matches) {
    applyColorTheme(true)
  } else {
    applyColorTheme(false)
  }
}

export async function initDarkMode() {
  if (!isSafariDarkModeSupport()) return
  const mode = String(getArgsFromUrl('mode'))
  matchExpr(mode, [
    ['0', () => applyColorTheme(false)],
    ['1', () => applyColorTheme(true)],
    ['2', () => enableDarkModeiOS()],
  ])
}
export const reportError = (eventName: string, err: Error, vm: string, info: string) => {
  const stack = err.stack?.split('\n').slice(0, 6).join('\n')
  const dict: EventDict = {
    str_param2: JSON.stringify({
      vm,
      info,
    }),
    str_param3: stack,
  }
  reportEvent(eventName, dict)
}
;(window as any).applyColorTheme = applyColorTheme

export function onDocumentClick(selector: string, fn: any) {
  document.addEventListener('click', (e) => {
    let el = e.target as any
    while (!el.matches(selector)) {
      console.warn('[target]', el)
      if (el === document.body) {
        el = null
        break
      }
      el = el.parentNode
    }
    if (el.matches(selector)) {
      fn.call(el, e, el)
    }
  })
}

