import { initLudo } from '@/main'
import { initBloot } from '@@/main'
import { initDiwania } from '@d/main'
import { api, getArgsFromUrl } from '@badam/common-utils'
import qs from 'qs'
import { fb } from './fb'
import { addVConsolePlugin, isProductType, ProductType } from './helper'

// baloot 传的是 bundleid
// ludo diwania 传的是 bundle_id
const bundle_id = getArgsFromUrl('bundle_id') || getArgsFromUrl('bundleid') || 'com.sawa.ludo'
const isBalootToLudo = bundle_id === 'com.sawa.ludo' && getArgsFromUrl('is_baloot') === '1'

function initBtn() {
  ;(function (d, s, id) {
    let js: any
    let fjs: any = d.getElementsByTagName(s)[0]
    if (d.getElementById(id)) {
      return
    }
    js = d.createElement(s)
    js.id = id
    js.src = 'https://connect.facebook.net/en_US/sdk.js'
    if (fjs) fjs.parentNode.insertBefore(js, fjs)
  })(document, 'script', 'facebook-jssdk')
  // @Td recode!
  ;(window as any).fbAsyncInit = function () {
    // 这里不改了，登录sawa都走 297601757621370
    // const pkg = getArgsFromUrl('bundleid')
    const pkg = bundle_id
    const is_diwania = getArgsFromUrl('is_diwania')
    fb.appid =
      ((pkg === 'com.sawa.ksa') || isBalootToLudo) // baloot 支持登录到 ksa 和 ludo，用同个appid
        ? '391521625027907' // 授权 baloot
        : process.env.VUE_APP_FLAVOR === 'weyay'
        ? '1666468373982706' // // 授权 weyay
        : is_diwania
        ? '7953232924746525' // 授权 diwania
        : '297601757621370' // 授权 ksa
    console.log('facebook初始化 包名', pkg, fb.appid)
    const v = 'v6.0'
    ;(window as any).FB.init({
      appId: fb.appid,
      xfbml: true,
      version: v,
    })
    ;(window as any).FB.AppEvents.logPageView()
  }
}

if (isProductType(ProductType.Diwania)) {
  initDiwania()
} else if (isProductType(ProductType.Ksa) || isBalootToLudo) {
  initBloot()
} else {
  // if (isProductType(ProductType.Ludo) || isProductType(ProductType.WeyayPlay))
  initLudo()
}

console.log('initBtn', fb.appid, bundle_id, process.env)
initBtn()

async function postGoogle() {
  const form = qs.stringify({
    client_id: '532022495714-s5hikassju5bdbsmsfc277pt4ro2ff9l.apps.googleusercontent.com',
    client_secret: '7SOVaeYJM2pu-8zVMcJ-_Eeq',
    grant_type: 'authorization_code',
    code: '4/0AVMBsJiXA7he8oSCrqS-2mFvstG2t5TaCLgVmg-8xjHjB5vA3rEsAIuJtnUPIc4coT1sAQ',
    redirect_uri: 'postmessage',
  })
  const res = await api
    .getAxiosInstance()
    .post('https://oauth2.googleapis.com/token', form, {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
    })
    .catch((err) => {
      console.error('Google OAuth 错误:', err)
      return { status: 500, data: err.message }
    })

  if (res.status === 200) {
    console.log('Google OAuth 成功:', res.data)
  } else {
    console.error('Google OAuth 失败:', res.status, res.data)
  }
}

// postGoogle()
;(window as any).getArgsFromUrl = getArgsFromUrl
addVConsolePlugin()
