import * as tsx from 'vue-tsx-support'

type GlobalRegister = {
  ['v-loading']?: boolean
}

export default class BaseComponent<P = unknown, E = unknown, S = unknown> extends tsx.Component<
  P & GlobalRegister,
  E,
  S
> {
  mounted() {}

  created() {}

  render() {}
}
