function router(url: string) {
  if (window.JavaBridgeObj && (window.JavaBridgeObj as any).router) {
    ;(window.JavaBridgeObj as any).router(url)
  } else {
    window.location.href = url
  }
}

export function refreshAccount(accountResp: any, source: string) {
  const { identities, user_info } = accountResp.data
  if (user_info) {
    const { session_key, uid, icon_verify } = user_info
    const o = { session_key, uid, icon_verify }
    const v = encodeURIComponent(JSON.stringify(o))
    console.warn('refreshAccount', user_info, v)
    if (getArgsFromUrl('is_debug') === '2') return
    const url = `${scheme}://sawaksa_login/?sawaksa_acount=${v}&source=${source}&is_recall=${recall_value}`
    console.warn('[router]', url)
    router(url)
  }
}

