export function refreshDeviceToken(
  mobile: number | null | string,
  country: string | null,
  flag: string | null
) {
  if (mobile) localStorage.setItem('ludo_refresh_device_token_mobile', `${mobile}` || '')
  if (country) localStorage.setItem('ludo_refresh_device_token_country', `${country}` || '')
  if (flag) localStorage.setItem('ludo_refresh_device_token_flag', `${flag}` || '')

  let href = location.href
  href = setUrlParams2('is_refresh', 1)
  href = `${scheme}://refresh_device_token/?url=${encodeURIComponent(href)}`
  console.warn('refreshDeviceToken', href)
  router(href)
}

export function getSub(access_token: string) {
  const arr = access_token.split('.')
  if (arr.length < 3) {
    return ''
  }
  try {
    const obj = arr[1]
    const subs = atob(obj)
    const o = JSON.parse(subs)
    const sub = o.sub
    return sub
  } catch (e) {
    return ''
  }
}

export function showBanToast(d: string) {
  const dd = Number(d)
  if (dd) {
    if (dd === -1) {
      showToast(_T('因涉嫌违规，你的账号被永久封禁。你无法再登录该账号。'))
    } else {
      const target = dayjs(dd * 1000)
        .utcOffset(3)
        .format('YYYY-MM-DD')
      showToast(
        sprintf(_T('因涉嫌违规，你的账号被封禁至{date}，这段时间内你将无法登陆该账号。'), {
          date: target,
        })
      )
    }
  }
}

export function versionToNumber(versionString: string) {
  const parts = versionString.split('.')

  // 确保至少有三位
  while (parts.length < 3) {
    parts.push('0')
  }

  let result = ''
  for (let i = 0; i < 3; i += 1) {
    let currentPart = parts[i]

    if (i > 0 && currentPart.length < 2) {
      currentPart = currentPart.padStart(2, '0')
    }

    result += currentPart
  }

  return parseInt(result, 10)
}

export function getAppClientVersion() {
  const version_code = getArgsFromUrl('version_code')
  if (!version_code) return ''
  // 处理参数有多个 version_code 的情况
  let vc = version_code
  if (Object.prototype.toString.call(version_code) === `[object ${'Array'}]`) {
    vc = version_code[0]
  }
  return versionToNumber(vc).toString()
}

