export function getLoadDuration() {
  const clientTs = getClientTs()
  if (!clientTs) {
    const duration = Math.floor(performance.now())
    return duration
  }
  setHashParams('client_ts', '')
  const duration = Math.floor(Date.now() - clientTs)
  return duration
}

export function setHashParams(
  key: string,
  value: string | number | boolean | undefined,
  replace = true
) {
  const [hashPath = '', hashQuery = ''] = window.location.hash.slice(1).split('?')
  const searchParams = new URLSearchParams(hashQuery)

  // 修改参数值
  if (value === undefined) {
    searchParams.delete(key)
  } else {
    searchParams.set(key, String(value))
  }

  const str = searchParams.toString()
  let newHash = hashPath
  if (str) {
    newHash += `?${str}`
  }

  const newUrl = `${window.location.pathname + window.location.search}#${newHash}`

  if (replace) {
    // 可选：更新浏览器地址栏（不刷新页面）
    window.history.replaceState({}, '', newUrl)
  }

  return newUrl
}

