import { api, ApiResult, getArgsFromUrl } from '@badam/common-utils'
import axios from 'axios'
import store from '@/store'
import { client_id, getAppClientVersion, isDiwania } from '@/utils'
import { getDeviceToken, isTargetVersion } from '@/utils/new-login'
