api.setWgetEnable(true)

export const isWeyay = process.env.VUE_APP_FLAVOR === 'weyay'

const wget = api.wget.bind(api)
const get = api.get.bind(api)
const post = api.post.bind(api)

if (getArgsFromUrl('test_rc')) {
  api.setHeader(() => ({
    'x-cli-rc': '',
  }))
}

function getAuthType() {
  const is_diwania = getArgsFromUrl('is_diwania')
  return is_diwania ? 4 : 1
}

export function loginWithApple(
  user_id: string,
  access_token: string,
  nickname?: string,
  auto_create = false
) {
  const auth_access = store.state.data
  const uuid = getArgsFromUrl('uuid') || ''
  const bundle_id = getArgsFromUrl('bundle_id') || ''
  const obj: any = {
    login_type: 'apple',
    user_id,
    access_token,
    // nickname,
    auth_type: getAuthType(),
    auth_access,
    auto_create,
    appid: client_id.apple,
  }
  let data: any = new URLSearchParams()
  obj && Object.keys(obj).forEach((key) => data.append(key, obj[key]))
  // data = qs.stringify(data)

  return post('/api/user_mode/get_login_key/', data, {
    'x-cli-br': 1,
    'x-cli-id': uuid,
    'x-cli-pn': bundle_id,
    'x-cli-ve': getAppClientVersion(),
    'content-type': 'application/x-www-form-urlencoded',
  } as any) as Promise<[boolean, ApiResult<any>]>
}

