export function loginWithMobile(
  mobile: string | number,
  code: string | number,
  country_code: string | number,
  auto_create = false
) {
  // const auth_access = store.state.data
  const uuid = getArgsFromUrl('uuid') || ''
  const bundle_id = getArgsFromUrl('bundle_id') || ''
  const obj: any = {
    login_type: 'mobile',
    mobile,
    code,
    country_code,
    auth_type: getAuthType(),
    // auth_access,
    auto_create,
  }

  let data: any = new URLSearchParams()
  obj && Object.keys(obj).forEach((key) => data.append(key, obj[key]))
  if (isTargetVersion()) {
    return api.wget('/api/user_mode/get_login_key/', obj) as Promise<[boolean, ApiResult<any>]>
  }
  return api.post('/api/user_mode/get_login_key/', data, {
    'x-cli-br': 1,
    'x-cli-id': uuid,
    'x-cli-pn': bundle_id,
    'x-cli-ve': getAppClientVersion(),
    'content-type': 'application/x-www-form-urlencoded',
  } as any) as Promise<[boolean, ApiResult<any>]>
}

export const addEvent = (event: BadamEvent) =>
  // if (conf.useMock) {
  //   return mock.commonRank()
  // }
  get('/api/sys/add_event/', {
    event_name: event.eventName,
    uuid: event.uuid,
    version: event.version,
    str_param2: event.dict.str_param2,
    str_param3: event.dict.str_param3,
    str_param4: event.dict.str_param4,
    str_param1: JSON.stringify({
      uid: event.uid,
      op: event.op,
      tk: event.tk,
      ua: event.ua,
      proj: event.proj,
      href: event.href,
      rid: event.rid,
    }),
    int_param1: event.dict.int_param1,
    int_param2: event.dict.int_param2,
    int_param3: event.dict.int_param3,
    int_param4: event.dict.int_param4,
  }) as unknown as Promise<[boolean, ApiResult<any>]>

/**
 *
 * 手机获取验证码
 * @export
 * @param {string} mobile 手机号码
 * @param {string} country_code 国家码
 * @return {*}
 */
export async function getMobileCode(mobile: string | number, country_code: string | number) {
  const uuid = getArgsFromUrl('uuid') || ''
  const bundle_id = getArgsFromUrl('bundle_id') || ''
  const device_token = getArgsFromUrl('device_token') || (await getDeviceToken()) || ''
  let obj
  if (device_token) {
    obj = {
      mobile,
      country_code,
      device_token,
      from_app_uuid: uuid,
      from_app_package_name: bundle_id,
      auth_type: getAuthType(),
    }
  } else {
    obj = {
      mobile,
      country_code,
      from_app_uuid: uuid,
      from_app_package_name: bundle_id,
      auth_type: getAuthType(),
    }
  }
  return api.wget3('/api/user/get_code/', obj, {
    'x-cli-br': 1,
    'x-cli-pn': isWeyay ? 'com.sawa.weyay.h5' : 'com.sawa.web.ksa',
  }) as Promise<[boolean, ApiResult<any>]>
}

// 获取绑定手机号可登录账号列表
export function queryBindedAccounts(param: QueryBindedAccountsParam) {
  const uuid = getArgsFromUrl('uuid') || ''
  const bundle_id = getArgsFromUrl('bundle_id') || ''
  const obj: any = param
  obj.auth_type = isDiwania.value ? 4 : 1
  let data: any = new URLSearchParams()
  obj && Object.keys(obj).forEach((key) => data.append(key, (obj as any)[key]))
  if (isTargetVersion()) {
    return api.wget('/api/mobile_login/query_identities/', obj) as Promise<
      [boolean, ApiResult<any>]
    >
  }
  return api.post('/api/mobile_login/query_identities/', data, {
    'x-cli-br': 1,
    'x-cli-id': uuid,
    'x-cli-pn': bundle_id,
    'content-type': 'application/x-www-form-urlencoded',
  } as any) as Promise<[boolean, ApiResult<any>]>
}

