import { api, ApiResult } from '@badam/common-utils'
import { addUserScript, client_id, getArgsFromUrl } from '@@/utils/func'
// import { ActivityRankingListRspMsg, CommonRankingListResult } from './interface'

api.setWgetEnable(false)

const wget = api.get.bind(api)
const get = api.get.bind(api)

function resolveApiBaseInDev() {
  const base = getArgsFromUrl('api_base')
  if (!base) return 'https://arab-pk-release.badambiz.com/'
  if (!base.includes('http')) return `https://${base}-pk.badambiz.com/`
  const v = decodeURIComponent(decodeURIComponent(base))
  const u = new URL(v).origin
  return u.slice(-1) === '/' ? u : `${u}/`
}

console.warn('resolveApiBaseInDev', resolveApiBaseInDev())

// api.setPn('com.sawa.ksa')
api.getAxiosInstance().defaults.baseURL =
  process.env.VUE_APP_ENV === 'development' || getArgsFromUrl('is_debug')
    ? resolveApiBaseInDev()
    : 'https://arab-pk.badambiz.com/'

const uuid = getArgsFromUrl('uuid') || ''
const bundle_id = getArgsFromUrl('bundleid') || getArgsFromUrl('bundle_id') || ''

export function cancelSawaAccountRevoke(ap_uid: string, ap_token: string, auto_create = false) {
  return wget(
    '/api/user_mode/get_login_key/',
    {
      login_type: 'third',
      channel: 'apple_baloot',
      access_token: ap_uid,
      channel_values: ap_token,
      device: 'web',
      auto_create,
    },
    {
      'x-cli-br': 1,
      'x-cli-id': uuid,
      'x-cli-pn': bundle_id,
    }
  ) as Promise<[boolean, ApiResult<any>]>
}

