import Vue from 'vue'
import { printf, sprintf, textWithLTR, _T } from '@@/utils/lang'
import '@@/assets/ar/css/login.scss'
import api, { loginWithFacebook2 } from '@@/api'
import { showModal } from '@@/components/modal/modal.vue'
import Dialog from '@@/components/modal/Dialog.vue'
import { getArgsFromUrl, handleApiResult } from '@badam/common-utils'
import { formatTs, unregisterRemind, setUrlParams, isBalootToLudo } from '@@/utils/func'
import { reportLoginClick, reportLoginFail, reportLoginPage, reportRevokeClick, reportRevokeShow } from '@@/utils/report'

export default Vue.extend({
  components: {},
  data() {
    return {
      loading: false,
      isRegister: !!getArgsFromUrl('is_register'),
      isBalootToLudo,
    }
  },

  mounted() {
    console.warn('Login2 [user_mode]')
    localStorage.setItem('index_url', window.location.href)
    this.bindEvent()
    // this.showDialogTest() // @Td remove, for test
    this.isRegister = !!getArgsFromUrl('is_register')
    console.log('in user mode >>', window.location.href, this.isRegister)
    reportLoginPage()
  },
  methods: {
    _T,
    toCreate() {
      // location.href = `${location.href}&is_register=1`
      const url = setUrlParams('is_register', '1')
      location.href = url
    },
    bindEvent() {
      document.addEventListener('AppleIDSignInOnSuccess', (data) => {
        console.log('Apple Succeed', data)
      })
      document.addEventListener('AppleIDSignInOnFailure', (error: any) => {
        console.log('Apple Failed', error)
        const reason = error.detail.error
        reportLoginFail('baloot_apple', `APPLE_SERV FAIL ${reason}`)
      })
    },
    async onPressApple() {
      console.log(1, 'click apple', location.href)
      await reportLoginClick('baloot_apple')
      if (this.loading) {
        return
      }
      this.loading = true
      try {
        const data = await AppleID.auth.signIn()
        console.log(2, 'apple info', data)
        if (!data || !data.authorization || !data.authorization.id_token) {
          reportLoginFail('baloot_apple', 'APPLE_SERV FAIL NO TOKEN')
          this.loading = false
          return
        }
        const { code: apple_uid, id_token: apple_token } = data.authorization
        this.doLoginApple(apple_uid, apple_token, this.isRegister)
      } catch (error) {
        this.loading = false
      }
    },
    async onPressFacebook() {
      console.log(1, 'click facebook login2')
      await reportLoginClick('baloot_fb')
      if (this.loading) {
        return
      }
      this.loading = true
      FB.login((response: any) => {
        console.log(2, 'facebook info', response)
        if (!response.authResponse) {
          reportLoginFail('baloot_fb', 'FB_SERV FAIL NO TOKEN')
          this.loading = false
          return
        }
        const { userID: fb_uid, accessToken: fb_token } = response.authResponse
        this.doLoginFacebook(fb_uid, fb_token, this.isRegister)
      })
    },
    async doLoginFacebook(fb_uid: string, fb_token: string, auto_create = false) {
      const [success, accountResp] = await api.loginWithFacebook2(fb_uid, fb_token, auto_create)
      console.log(3, accountResp)
      this.loading = false
      if (!success) {
        reportLoginFail('baloot_fb', 'SAWA_SERV FAIL NOT 200')
        ;(this as any).$toast(_T('获取数据失败，请稍后重试'))
        this.loading = false
        return
      }
      if (accountResp.result !== 0) {
        this.loading = false
        if (accountResp.result === 20193 && !accountResp.extra?.baloot_cancel) {
          unregisterRemind(accountResp)
          return
        }
        // 数据上报
        const e: any = {
          10001: '无baloot账号',
          20160: 'baloot账号登录，新注册的账号不支持',
        }
        const reportMsg = e[accountResp.result] || accountResp.message
        if (accountResp.extra && accountResp.extra.baloot_cancel) {
          reportLoginFail('baloot_fb', 'baloot账号已申请注销')
        }
        else {
          reportLoginFail('baloot_fb', `SAWA_SERV FAIL(${accountResp.result}) ${reportMsg}`)
        }
        // 错误弹窗
        this.showDialog(accountResp, fb_uid, fb_token, 'fb')
        return
      }
      this.refreshAccount(accountResp, 'baloot_fb', { uid: fb_uid, token: fb_token })
    },
    async doLoginApple(apple_uid: string, apple_token: string, auto_create = false) {
      const [success, accountResp] = await api.loginWithApple2(apple_uid, apple_token, auto_create)
      console.log(3, accountResp)
      this.loading = false
      if (!success) {
        reportLoginFail('baloot_apple', 'SAWA_SERV FAIL NOT 200')
        ;(this as any).$toast(_T('获取数据失败，请稍后重试'))
        this.loading = false
        return
      }
      if (accountResp.result !== 0) {
        this.loading = false
        if (accountResp.result === 20193 && !accountResp.extra?.baloot_cancel) {
          unregisterRemind(accountResp)
          return
        }
        // 数据上报
        const e: any = {
          10001: '无baloot账号',
          20160: 'baloot账号登录，新注册的账号不支持',
        }
        const reportMsg = e[accountResp.result] || accountResp.message
        if (accountResp.extra && accountResp.extra.baloot_cancel) {
          reportLoginFail('baloot_apple', 'baloot账号已申请注销')
        }
        else {
          reportLoginFail('baloot_apple', `SAWA_SERV FAIL(${accountResp.result}) ${reportMsg}`)
        }
        // 错误弹窗
        this.showDialog(accountResp, apple_uid, apple_token, 'apple')
        return
      }
      this.refreshAccount(accountResp, 'baloot_apple', { uid: apple_uid, token: apple_token })
    },
    refreshAccount(accountResp: any, source: string, p: { uid: string; token: string }) {
      console.log(4, 'login ok', accountResp)
      if (getArgsFromUrl('stay')) {
        return
      }
      const { identities, user_info } = accountResp.data
      // 只有1个身份的情况
      if (user_info) {
        // if (getArgsFromUrl('for_test')) { // @Td remove
        //   user_info.baloot_cancel = true
        //   user_info.baloot_uid = '0'
        // }
        const { session_key, uid, icon_verify, baloot_cancel, baloot_uid } = user_info
        if (baloot_cancel) {
          reportLoginFail(source, 'baloot账号已申请注销')
          reportRevokeShow(baloot_uid)
          showModal(Dialog, {
            item: {
              title: _T('登录失败'),
              content: _T('该baloot账号已申请注销，若想授权登录sawa，需要撤销注销申请。是否撤销申请？'),
              confirmText: _T('确定'),
              cancelText: _T('取消'),
              cancelCallback: () => {
                reportRevokeClick('返回', baloot_uid)
              },
              confirmCallback: async () => {
                reportRevokeClick('撤销申请', baloot_uid)
                const res = await api.revokeBalootCancel(session_key)
                if (handleApiResult(res)) {
                  if (source === 'baloot_fb') {
                    this.doLoginFacebook(p.uid, p.token, false)
                  } else if (source === 'baloot_apple') {
                    this.doLoginApple(p.uid, p.token, false)
                  }
                } else {
                  const [, { data, toast }] = res
                  ;(this as any).$toast(toast || _T('获取数据失败，请稍后重试'))
                }
              },
            },
          })
          return
        }
        const o = { session_key, uid, icon_verify }
        const v = encodeURIComponent(JSON.stringify(o))
        const schema = this.isBalootToLudo ? 'sawaludo' : 'sawaksa'
        window.location.href = `${schema}://baloot_login/?baloot_acount=${v}&source=${source}`
        return
      }
      // 多个身份的情况
      const b = (identities || []).map((e: any) => e.login_key)
      const u = encodeURIComponent(JSON.stringify(b))
      const schema = this.isBalootToLudo ? 'sawaludo' : 'sawaksa'
      window.location.href = `${schema}://baloot_login/?identities=${u}&source=${source}`
    },
    showDialog(accountResp: any, uid: string, token: string, type: string) {
      const { result, message, extra } = accountResp
      // if (getArgsFromUrl('for_test')) { // @Td remove
      //   extra.baloot_cancel = true
      //   extra.baloot_uid = '0'
      // }
      if (result === 20310) {
        showModal(Dialog, {
          item: {
            title: _T('创建账号'),
            content: _T('你还没有baloot账号，是否马上创建账号并登录sawa？'),
            confirmText: _T('确定'),
            cancelText: _T('取消'),
            confirmCallback: () => {
              if (type === 'fb') {
                this.doLoginFacebook(uid, token, true)
              } else if (type === 'apple') {
                this.doLoginApple(uid, token, true)
              }
            },
          },
        })
        return
      }
      if (extra) {
        const { session_key, baloot_cancel, baloot_uid } = extra
        if (baloot_cancel) {
          reportRevokeShow(baloot_uid)
          showModal(Dialog, {
            item: {
              title: _T('登录失败'),
              content: _T('该baloot账号已申请注销，若想授权登录sawa，需要撤销注销申请。是否撤销申请？'),
              confirmText: _T('确定'),
              cancelText: _T('取消'),
              cancelCallback: () => {
                reportRevokeClick('返回', baloot_uid)
              },
              confirmCallback: async () => {
                reportRevokeClick('撤销申请', baloot_uid)
                const res = await api.revokeBalootCancel(session_key)
                if (handleApiResult(res)) {
                  if (type === 'fb') {
                    this.doLoginFacebook(uid, token, false)
                  } else if (type === 'apple') {
                    this.doLoginApple(uid, token, false)
                  }
                } else {
                  const [, { data, toast }] = res
                  ;(this as any).$toast(toast || _T('获取数据失败，请稍后重试'))
                }
              },
            },
          })
          return
        }
      }

      let reason = _T('账号登录失败，请重新登录。')

      if (result === 20181) {
        reason = _T('所选账号已被停用，无法登录该账号。请重新登录。')
      } else if (result === 20001) {
        const banTo = +message
        if (banTo === -1) {
          reason = _T('因涉嫌违规，你的账号被永久封禁。你无法再登录该账号。')
        } else if (banTo > 0) {
          reason = sprintf(
            _T('因涉嫌违规，你的账号被封禁至%s，这段时间内你将无法登陆该账号。'),
            `<i>${formatTs(banTo)}</i>`
          )
        }
      }
      //
      showModal(Dialog, {
        item: {
          title: _T('登录失败'),
          content: reason,
          confirmText: _T('我知道了'),
          confirmCallback: () => {
            ;(window as any).location.href = localStorage.getItem('index_url')
          },
        },
      })
    },
    showDialogTest() {
      // this.showDialog(
      //   {
      //     result: 20001,
      //     message: Math.floor(+new Date() / 1000),
      //   },
      //   '0000',
      //   'ssss',
      //   'apple'
      // )

      const type = 'fb'
      const uid = 'xxxx'
      const token = 'xxx'

      showModal(Dialog, {
        item: {
          title: _T('创建账号'),
          content: _T('你还没有baloot账号，是否马上创建账号并登录sawa？'),
          confirmText: _T('确定'),
          cancelText: _T('取消'),
          confirmCallback: () => {
            if (type === 'fb') {
              this.doLoginFacebook(uid, token, true)
            } else if (type === 'apple') {
              this.doLoginApple(uid, token, true)
            }
          },
        },
      })
    },
    onPressPrivacy() {
      reportLoginClick('隐私政策')
      const link = 'https://arabpk-game.badambiz.com/html/about_en.html'
      window.location.href = link
    },
    onPressAgreement() {
      reportLoginClick('服务条款')
      const link = 'https://arabpk-game.badambiz.com/html/about_en.html'
      window.location.href = link
    },
  },
  computed: {
    descV(): string {
      return this.isBalootToLudo ?
        _T('游客请在Baloot绑定账号才可在Jackaroo登录') : _T('游客请在Baloot绑定账号才可在Sawa登录')
    }
  }
})
