import { getArgsFromUrl, initWebSensors, SensorSDKName, reportWebSensors } from '@badam/common-utils'

export async function initWebSa(serv_url: string) {
  const sa_sdk_js = './sensorsdata.min.v1.js'
  if (process.env.VUE_APP_ENV === 'development') {
    console.warn('[initSa] Test')
    await initWebSensors(SensorSDKName.TEST, true)
  }
  else {
    console.warn('[initSa] KSA')
    await initWebSensors(SensorSDKName.KSA, false, 'beacon', serv_url, sa_sdk_js)
  }
}
