import { getArgsFromUrlHash, getPlatform, matchExpr } from '@badam/common-utils'

export enum ProductType {
  Are = 'are',
  Ksa = 'ksa',
  Ludo = 'ludo',
  Diwania = 'diwania',
  Baloot = 'baloot',
  Weyay = 'weyay',
  WeyayPlay = 'weyayplay',
  // VIDEO = "video",
}

const bundleid = getArgsFromUrlHash('bundle_id') || getArgsFromUrlHash('bundleid')

export function getProductType() {
  const v = matchExpr(
    bundleid,
    [
      ['com.sawa.ksa', ProductType.Ksa], // ios
      ['com.badambiz.pk.saudi', ProductType.Ksa], // 安卓
      ['com.badambiz.mideastsau', ProductType.Baloot], // ios
      ['hk.longchiu.dwn', ProductType.Diwania], // ios
      ['com.sawa.weyay', ProductType.Weyay], // ios
      ['com.sawa.weyayjackaroo', ProductType.WeyayPlay], // ios
      ['com.sawa.ludo', ProductType.Ludo], // ios
      ['com.badambiz.pk.saudi.jackaroo', ProductType.Ludo], // 安卓正式
      ['com.badambiz.pk.saudi.jackaroo.dev', ProductType.Ludo], // 安卓测试
    ],
    () => ProductType.Ksa
  )
  return v
}

export function isProductType(productType: ProductType) {
  return getProductType() === productType
}

export function isProductAndroidLudo() {
  return ['com.badambiz.pk.saudi.jackaroo', 'com.badambiz.pk.saudi.jackaroo.dev'].includes(bundleid)
}

export function getDefaultAreaCode() {
  if (isProductType(ProductType.Weyay) || isProductType(ProductType.WeyayPlay)) {
    return '964'
  }
  return '966'
}

export function getDefaultAreaFlag() {
  if (isProductType(ProductType.Weyay) || isProductType(ProductType.WeyayPlay)) {
    return 'IQ'
  }
  return 'SA'
}

export async function addVConsolePlugin() {
  if (process.env.VUE_APP_ENV === 'production') {
    return
  }
  try {
    // 创建 script 标签
    const script = document.createElement('script')
    script.src = 'https://unpkg.com/vconsole@latest/dist/vconsole.min.js'
    script.async = true
    // 返回 Promise 来处理脚本加载
    await new Promise((resolve, reject) => {
      script.onload = () => {
        const vConsole = new (window as any).VConsole()
        resolve(null)
      }
      script.onerror = () => {
        console.error('Failed to load VConsole script')
        reject(new Error('Failed to load VConsole script'))
      }
      document.head.appendChild(script)
    })
    // 脚本加载完成后执行 log 函数
    if (typeof (window as any).VConsole !== 'undefined') {
      const vConsole = new (window as any).VConsole()
      console.log('VConsole initialized and ready to use!')
    } else {
      console.warn('VConsole is not available after script load')
    }
  } catch (error) {
    console.error('Error in debug function:', error)
  }
}
