/* eslint-disable import/prefer-default-export */
import Vue from 'vue'
import 'vue-tsx-support/enable-check'
import App from './App'
import store from './store'
import { calculateWindow2, initAppleLogin, loadVConsoleIfNeed, isBalootToLudo } from './utils/func'
import { initErrorHandler } from './utils/event'
import { initWebSa } from './utils/report'
import { getConfigAndDial } from './utils/dial'
import FirebaseManager from './utils/firebase-manager'

Vue.config.productionTip = false

export const initBloot = async () => {
  await FirebaseManager.getInstance().init()
  await getConfigAndDial()

  initAppleLogin()

  initErrorHandler()

  calculateWindow2()

  loadVConsoleIfNeed()

  if (isBalootToLudo) {
    document.body.classList.add('-ludo')
  }

  new Vue({
    store,
    render: (h) => h(App),
  }).$mount('#app')
}
