import { getArgsFromUrl } from '@@/utils/func'

const v = (val: any, defaultVal: any) => {
  if (val === undefined) {
    return defaultVal
  }
  return val
}

const useMockAuto = !(window as any).kinoJsApi || !(window as any).JavaBridgeObj

const conf = {
  useLog: v(getArgsFromUrl('use_log'), false),
  useMock: v(getArgsFromUrl('use_mock'), useMockAuto),
  isDebug: v(getArgsFromUrl('is_debug'), false),
  showAppBar: getArgsFromUrl('show') === '0',
  clientTs: v(getArgsFromUrl('client_ts'), false), // 是否使用客户端时间，默认否 会校准服务器时间
}

export const log = (...args: any) => {
  if (!conf.useLog) {
    return
  }
  console.log(...args)
}

export default conf
