import lan from '@@/lang/index.ar.json'
import { getArgsFromUrl } from './func'

const language = getArgsFromUrl('lang') || getArgsFromUrl('lan')

export function _T(s: string) {
  const sheet = 'sawaksa增加baloot登录方式'
  const lang: any = lan
  if (language === 'zh') {
    return s
  }
  // console.log(s, lan)
  const mapped = lang[s] || lang[`${s}{#${sheet}#}`]
  if (mapped) {
    return mapped
  }
  return s.replace(/\{#.*#\}/g, '')
}

export function textWithRTL(text: string) {
  if (!text) {
    return ''
  }
  return `\u202B${text}\u202B`
}

export function textWithLTR(text: string) {
  if (!text) {
    return ''
  }
  return `\u202A${text}\u202A`
}

export function sprintf(expr: string, ...params: any[]) {
  if (!expr) {
    return ''
  }
  let i = 0
  const ret = expr.replace(/%s/g, () => params[i++])
  return textWithRTL(ret)
}

export function printf(expr: string, params: any) {
  if (!expr) {
    return ''
  }
  const ret = expr.replace(/%\[(\w)*\]/g, (item) => {
    const key = item.split('%[')[1]
    const value = key.split(']')[0]
    return params[value] === undefined || null ? '' : params[value]
  })
  return textWithRTL(ret)
}

// 数字显示处理
export function formatCount(count: number, product = 'live') {
  if (product === 'live') {
    if (count > 10000) {
      return `${(count / 10000).toFixed(2)}w`
    }
    return count.toString()
  }
  if (count > 1000 && count <= 1000000) {
    return `${(count / 1000).toFixed(2)}K`
  }
  if (count > 1000000) {
    return `${(count / 1000000).toFixed(2)}M`
  }
  return count.toString()
}

export function resizeImage(qiniuURL: string, width = 240) {
  // @see https://developer.qiniu.com/dora/1279/basic-processing-images-imageview2
  return `${qiniuURL}?imageView2/2/w/${width}`
}

// live专用
export function sslFix(url: string, width = 240) {
  if (!url) {
    return url
  }
  const a = /http[s]?:\/\/assets\.zvod\.badambiz\.com/
  const b = 'https://assets-ssl.zvod.badambiz.com'
  const c = url.replace(a, b)
  const d = width ? resizeImage(c, width) : c // width:0，表示不需要裁剪的情况
  return d
}

export const LangMixin = {
  methods: {
    _T,
    sprintf,
    textWithRTL,
    formatCount,
    textWithLTR,
    sslFix,
    printf,
  },
}

export default {
  _T,
  sprintf,
  textWithRTL,
  formatCount,
  textWithLTR,
  sslFix,
  printf,
  LangMixin,
}

export type LangMixin = {
  _T?: any
  sprintf?: any
  textWithRTL?: any
  formatCount?: any
  textWithLTR?: any
  sslFix?: any
  printf?: any
}
