
/* eslint-disable no-undef */
/* eslint-disable camelcase */
import Vue from 'vue'
import { textWithRTL, _T } from '@@/utils/lang'
import '@@/assets/ar/css/login.scss'
import api from '@@/api'
import bridge from '@@/utils/bridge'
import { getArgsFromUrl, jumpSchema, isBalootToLudo } from '@@/utils/func'
import { reportLoginClick, reportLoginFail, reportLoginPage, reportSa } from '@@/utils/report'

export default Vue.extend({
  components: {},
  // created() {},
  // beforeDestroy() {},
  data() {
    return {
      loading: false,
      isBalootToLudo,
      // startTs: 0,
    }
  },
  mounted() {
    console.warn('in old mode >>', window.location.href)
    this.bindEvent()
    console.warn('isBalootToLudo', this.isBalootToLudo)
    reportLoginPage()
  },
  methods: {
    bindEvent() {
      document.addEventListener('AppleIDSignInOnSuccess', (data) => {
        console.log('Apple Succeed', data)
      })
      document.addEventListener('AppleIDSignInOnFailure', (error: any) => {
        console.log('Apple Failed', error)
        const reason = error.detail.error
        reportLoginFail('baloot_apple', `APPLE_SERV FAIL ${reason}`)
      })
    },
    async onPressApple() {
      console.log(1, 'click apple')
      await reportLoginClick('baloot_apple')
      if (this.loading) {
        return
      }
      this.loading = true
      try {
        const data = await AppleID.auth.signIn()
        console.log(2, 'apple info', data)
        if (!data || !data.authorization || !data.authorization.id_token) {
          reportLoginFail('baloot_apple', 'APPLE_SERV FAIL NO TOKEN')
          this.loading = false
          return
        }
        const { code: apple_uid, id_token: apple_token } = data.authorization
        this.doLoginApple(apple_uid, apple_token)
      } catch (error) {
        this.loading = false
      }
    },
    async onPressFacebook() {
      console.log(1, 'click facebook')
      await reportLoginClick('baloot_fb')
      if (this.loading) {
        return
      }
      this.loading = true
      FB.login(async (response: any) => {
        console.log(2, 'facebook info', response)
        if (!response.authResponse) {
          reportLoginFail('baloot_fb', 'FB_SERV FAIL NO TOKEN')
          this.loading = false
          return
        }
        const { userID: fb_uid, accessToken: fb_token } = response.authResponse
        this.doLoginFacebook(fb_uid, fb_token)
      })
    },
    async doLoginFacebook(fb_uid: string, fb_token: string) {
      const [success, accountResp] = await api.loginWithFacebook(fb_uid, fb_token)
      console.log(3, accountResp)
      this.loading = false
      if (!success) {
        reportLoginFail('baloot_fb', 'SAWA_SERV FAIL NOT 200')
        ;(this as any).$toast(_T('获取数据失败，请稍后重试'))
        this.loading = false
        return
      }
      if (accountResp.result !== 0) {
        const d: any = {
          10001: textWithRTL(_T('您没有baloot账号，无法登录')),
          20160: textWithRTL(_T('只支持在2021年10月14号前注册baloot账号登录，新注册的账号不支持')),
        }
        const message = d[accountResp.result] || accountResp.message
        ;(this as any).$toast(message)
        const e: any = {
          10001: '无baloot账号',
          20160: 'baloot账号登录，新注册的账号不支持',
        }
        const reportMsg = e[accountResp.result] || accountResp.message
        reportLoginFail('baloot_fb', `SAWA_SERV FAIL(${accountResp.result}) ${reportMsg}`)
        this.loading = false
        return
      }
      this.refreshAccount(accountResp, 'baloot_fb')
    },
    async doLoginApple(apple_uid: string, apple_token: string) {
      const [success, accountResp] = await api.loginWithApple(apple_uid, apple_token)
      console.log(3, accountResp)
      this.loading = false
      if (!success) {
        reportLoginFail('baloot_apple', 'SAWA_SERV FAIL NOT 200')
        ;(this as any).$toast(_T('获取数据失败，请稍后重试'))
        this.loading = false
        return
      }
      if (accountResp.result !== 0) {
        const d: any = {
          10001: _T('您没有baloot账号，无法登录'),
          20160: _T('只支持在2021年10月14号前注册baloot账号登录，新注册的账号不支持'),
        }
        const message = d[accountResp.result] || accountResp.message
        ;(this as any).$toast(message)
        const e: any = {
          10001: '无baloot账号',
          20160: 'baloot账号登录，新注册的账号不支持',
        }
        const reportMsg = e[accountResp.result] || accountResp.message
        reportLoginFail('baloot_apple', `SAWA_SERV FAIL(${accountResp.result}) ${reportMsg}`)
        this.loading = false
        return
      }
      this.refreshAccount(accountResp, 'baloot_apple')
    },
    refreshAccount(accountResp: any, source: string) {
      console.log(4, 'login ok', accountResp)
      if (getArgsFromUrl('stay')) {
        return
      }
      // #1
      const { session_key, uid, icon_verify } = accountResp.data
      const o = { session_key, uid, icon_verify }
      const v = encodeURIComponent(JSON.stringify(o))
      const schema = this.isBalootToLudo ? 'sawaludo' : 'sawaksa'
      window.location.href = `${schema}://baloot_login/?baloot_acount=${v}&source=${source}`
    },
    onPressPrivacy() {
      reportLoginClick('隐私政策')
      const link = 'https://arabpk-game.badambiz.com/html/about_en.html'
      window.location.href = link
    },
    onPressAgreement() {
      reportLoginClick('服务条款')
      const link = 'https://arabpk-game.badambiz.com/html/about_en.html'
      window.location.href = link
    },
  },
  computed: {
    descV(): string {
      return this.isBalootToLudo ?
        _T('游客请在Baloot绑定账号才可在Jackaroo登录') : _T('游客请在Baloot绑定账号才可在Sawa登录')
    }
  }
})
