// 根据key获取url参数的值
export const getArgsFromUrl = (key: string): any => {
  const url = decodeURIComponent(window.location.href)
  const data = url.split('?')
  let params = ''
  if (data.length > 1) {
    // eslint-disable-next-line prefer-destructuring
    params = data[1]
  }
  const obj = qs.parse(params) || {}
  return obj[key] ? obj[key] : null
}

// 判断是否iOS
export const isIOS = () => {
  const platform = getArgsFromUrl('platform')
  const u = navigator.userAgent
  const ua = u.toLowerCase()
  return /iphone|ipad|ipod/.test(ua) || platform === 'iOS'
}

// 屏幕适配方案
export function calculateWindow(size = 720) {
  const { width } = window.screen
  const height = Math.min(window.screen.height, window.innerHeight)
  if (height < 5) {
    setTimeout(() => {
      calculateWindow()
    }, 16)
    return
  }
  const scale = Number((width / size).toFixed(3))
  const meta: any = document.getElementById('viewport-config')
  meta.name = 'viewport'
  meta.content = `width=device-width,initial-scale=${scale},user-scalable=no,maximum-scale=${scale},minimum-scale=${scale},viewport-fit=cover`
  document.documentElement.style.width = `${size}px`
  if (isIOS()) {
    document.documentElement.style.height = '100vh'
  } else {
    document.documentElement.style.height = `${window.screen.height / scale}px`
  }
}

export function calculateWindow2(cb1 = () => {}, cb2 = () => {}) {
  const height = Math.min(window.screen.height, window.innerHeight)
  if (height < 5) {
    setTimeout(() => {
      calculateWindow()
    }, 16)
    return
  }
  const meta: any = document.getElementById('viewport-config')
  meta.name = 'viewport'
  meta.content =
    'width=device-width,initial-scale=1,user-scalable=no,maximum-scale=1,minimum-scale=1,viewport-fit=cover'
  if (isIOS()) {
    document.documentElement.style.height = '100vh'
  }
  cb1 && cb1()
  cb2 && cb2()
}

// 获取open id
export const getOpenid = (): any =>
  new Promise((resolve, reject) => {
    try {
      ;(window as any).kinoJsApi.getUserInfo((res: any) => {
        resolve(res.openid)
      })
    } catch (e) {
      resolve('')
    }
  })

// kinoJsApi 初始化
export const awaitKinoJsApi = (): Promise<boolean> => {
  let times = 0
  return new Promise((resolve) => {
    const waitForWindow = () => {
      times += 1
      setTimeout(() => {
        if (
          !(
            (window as any).kinoJsApi &&
            (window as any).kinoJsApi.getUserInfo &&
            (!isIOS() || (isIOS() && (window as any).webkit.messageHandlers.appCall))
          ) &&
          times < 50
        ) {
          // eslint-disable-next-line no-unused-vars
          waitForWindow()
        } else if (times >= 50) {
          resolve(false)
        } else {
          resolve(true)
        }
      }, 16)
    }
    waitForWindow()
  })
}

