// 动态添加脚本url
export const addDynamicScript = (src: string, integrity = '', crossorigin = 'anonymous') =>
  new Promise((resolve) => {
    const script = document.createElement('script')
    script.src = src
    // script.setAttribute('type', 'text/javascript')
    // script.defer = true
    // script.charset = 'utf-8'
    integrity ? (script.integrity = integrity) : void 0
    crossorigin ? (script.crossOrigin = crossorigin) : void 0
    document.body.appendChild(script)
    script.onload = () => {
      resolve(true)
    }
    script.onerror = () => {
      resolve(false)
    }
  })

// 获取全部参数，跳转路由用
export const getAllArgs = () => {
  const url = decodeURIComponent(window.location.href)
  const data = url.split('?')
  let params = ''
  if (data.length > 1) {
    // eslint-disable-next-line prefer-destructuring
    params = data[1]
  }
  const obj = qs.parse(params) || {}
  return obj
}

export function useFunc() {
  function addDynamic(src: string) {
    const script = document.createElement('script')
    script.setAttribute('type', 'text/javascript')
    script.src = src
    script.async = true
    script.charset = 'utf-8'
    document.body.appendChild(script)
  }

  function getArgFromUrl(key: string) {
    const str = location.search
    if (!str || str.substr(0, 1) !== '?') {
      return undefined
    }
    const obj: any = {}
    const items = str.substr(1).split('&')
    items.forEach((item) => {
      const r = item.split('=')
      const k = r[0]
      const v = decodeURIComponent(r[1])
      obj[k] = v
    })
    return obj[key]
  }

  function checkURL(url: any) {
    if (!url) {
      return false
    }
    return true
  }
  function main() {
    const ARG_NAME = 'use_fn'
    const url = getArgFromUrl(ARG_NAME)
    if (!checkURL(url)) {
      return
    }
    addDynamic(url)
  }
  main()
}

