export function throttle(fn: any, threshhold: number) {
  let last: any
  let timer: any
  threshhold || (threshhold = 250)
  return () => {
    const args = arguments
    const now = +new Date()
    if (last && now < last + threshhold) {
      clearTimeout(timer)
      timer = setTimeout(() => {
        last = now
        fn(args)
      }, threshhold)
    } else {
      last = now
      fn(args)
    }
  }
}

