/**
 *  检测环境是否支持webp图片
 *
 */
export const isSupportWebp = () => {
  const ele = window.document.createElement('canvas')
  if (ele.getContext && ele.getContext('2d')) {
    return ele.toDataURL('image/webp').indexOf('webp') > -1
  }
  // 不支持canvas,那么一定不支持webp
  return false
}

export function convertTsx<P>(comp: any) {
  return tsx.ofType<P>().convert(comp)
}

export function postForm(data: any) {
  const f = document.createElement('form')
  f.style.display = 'none'
  f.method = 'post'
  f.action = 'https://isawa.sawalive.cn/anyjump'
  f.target = '_target'
  for (const k of Object.keys(data)) {
    const input = document.createElement('input')
    input.type = 'hidden'
    input.name = k
    input.value = data[k]
    f.appendChild(input)
  }
  document.body.appendChild(f)
  console.log('sumbit form', data)
  f.submit()
  document.body.removeChild(f)
}

export function jumpSchema(schema: string) {
  postForm({ target: schema })
}

