
/* eslint-disable global-require */
/* eslint-disable @typescript-eslint/no-var-requires */
/* eslint-disable camelcase */
/* eslint-ignore */

import Vue from 'vue'
import { _T, getArgsFromUrl, getArgsFromUrlHash, getPlatform, getSysTs, sprintf } from '@badam/common-utils'
import { showToast } from '@/components/toast/toast.vue'
import store from '@/store'
import { loginWithApple, loginWithFacebook, loginWithGoogle } from '@/api'
import { Events, reportEvent } from '@/event'
import {
  CLIENT_ID,
  getLoadDuration,
  getSub,
  isDiwania,
  refreshAccount,
  showBanToast,
  sleepBlock,
  toDiwania,
  unregisterRemind,
} from '@/utils'
import { showModal } from '@/components/base/modal/modal.vue'

import { callbackToClient, goToMobileWebview, isTargetVersion } from '@/utils/new-login'
import { resolveApiBase, sleep } from '@d/utils/dial'
import CreateModal from './create-modal/index.vue'
import { isProductAndroidLudo } from '../../helper'

declare let AppleID: any
declare let FB: any

export default Vue.extend({
  // eslint-disable-next-line vue/no-unused-components
  components: { CreateModal },
  data() {
    return {
      appleLoginVisual: !isProductAndroidLudo(),
      loading: false,
      sdkLoaded: true,
      isDiwania,
      // isWeyayplay: process.env.VUE_APP_FLAVOR === 'weyay' && (getArgsFromUrlHash('bundleid') === 'com.sawa.weyayjackaroo'),
      isWeyay: process.env.VUE_APP_FLAVOR === 'weyay',
      isRelease: resolveApiBase().indexOf('arab-pk-release.badambiz') >= 0,
      codeClient: null as any,
      // use_new_google:
      //   !!getArgsFromUrl('use_new_google') ||
      //   resolveApiBase().indexOf('test10') >= 0 ||
      //   resolveApiBase().indexOf('release') >= 0,
      use_new_google: true,
      noDiwania: !!Number(getArgsFromUrl('no_diwania') || '0'),
      noAndroidLudo: isProductAndroidLudo(),
    }
  },
  mounted() {
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    //

    if (localStorage.getItem('firstCome')) {
      const p: any = {
        is_first_time: false,
      }

      const userType = getArgsFromUrl('user_type')
      if (userType) {
        p.type = decodeURIComponent(userType)
      }
      reportEvent(Events.h5_loginpage, p)
    } else {
      localStorage.setItem('firstCome', '1')
      const p: any = {
        is_first_time: true,
      }
      const userType = getArgsFromUrl('user_type')
      if (userType) {
        p.type = decodeURIComponent(userType)
      }
      reportEvent(Events.h5_loginpage, p)
    }
    reportEvent(Events.h5_loginpage_loading_finish, {
      source: decodeURIComponent(getArgsFromUrl('page_source') || ''),
      duration: getLoadDuration(),
    })

    if (getArgsFromUrl('is_refresh')) {
      this.$router.push({ name: 'Mobile' })
    }
  },
  async created() {
    // todo
  },
  computed: {
    appStyle(): any {
      if (this.isWeyay) {
        return '-weyay'
      }
      return ''
    },
    googleLoad(): any {
      return store.state.googleLoad
    },
    policy(): any {
      const src1 = this.isWeyay
        ? 'https://h5.weyay.chat/html/docs/weyay/term_of_service.html'
        : isDiwania.value
        ? 'https://isawa.badambiz.com/html/docs/diwania/user_agreement.html'
        : 'https://isawa.badambiz.com/html/user_agreement_ksa.html'
      const src2 = this.isWeyay
        ? 'https://h5.weyay.chat/html/docs/weyay/privacy_policy.html'
        : isDiwania.value
        ? 'https://isawa.badambiz.com/html/docs/diwania/privacy_policy.html'
        : 'https://isawa.badambiz.com/html/privacy_policy_ksa.html'
      return sprintf(
        _T('点击登录意味着你已经同意遵守{appname}的{terms_of_service}和{privacy_policy}'),
        {
          appname: this.isWeyay ? 'Weyay' : isDiwania.value ? 'Diwania' : 'Sawa',
          terms_of_service: `<a href=${src1} class='a'>${_T('服务条款')}</a>`,
          privacy_policy: `<a href=${src2} class='a'>${_T('隐私政策')}</a>`,
        }
      )
    },
  },
  watch: {
    googleLoad(newValue, oldValue) {
      if (newValue) {
        this.attachSignin()
        setTimeout(() => {
          this.sdkLoaded = true
        }, 100)
      }
    },
  },
  methods: {
    isOverWH(t: number) {
      return window.innerWidth / window.innerHeight > t
    },
    _T,
    sprintf,
    toDiwania,
    async onPressApple() {
      try {
        this.loading = true
        reportEvent(Events.h5_loginpage_elementclick, {
          element_name: 'apple',
        })
        const data = await AppleID.auth.signIn()
        if (!data || !data.authorization || !data.authorization.id_token) {
          this.loading = false
          reportEvent(Events.user_login_report_failed, {
            source: 'sawa',
            error_code: `获取apple token失败`,
            error_content: '获取apple token失败',
            is_sawaapp_authorize: false,
            h5_type: 'apple',
            type: '登录',
            h5_scene: 'get_token',
          })
          return
        }

        const { code: apple_uid, id_token: apple_token } = data.authorization
        const user_id = getSub(apple_token)
        this.loading = false
        this.doLoginApple(user_id, apple_token)
      } catch (error) {
        this.loading = false
      }
    },
    async onPressGoogle() {
      if (!this.use_new_google) {
        return
      }
      //
      // ① 等埋点真正返回
      reportEvent(Events.h5_loginpage_elementclick, {
        element_name: 'google',
      })

      // ② 强制延迟 400 ms
      await sleep(400)

      console.log('onPressGoogle')

      // ③ 手动拉起 Google 登录

      if (this.codeClient) {
        this.codeClient.requestCode()
        return
      }
    },
    async onPhonePress() {
      //
      reportEvent(Events.h5_loginpage_elementclick, {
        element_name: 'phone',
      })
      await sleep(400)
      if (isTargetVersion()) {
        goToMobileWebview()
        return
      }
      this.$router.push({ name: 'Mobile' })
    },
    async onPressFacebook() {
      console.log('onPressFacebook')
      reportEvent(Events.h5_loginpage_elementclick, {
        element_name: 'facebook',
      })
      await sleep(400)
      FB.login((response: any) => {
        console.log('response', response)
        if (!response.authResponse) {
          this.loading = false
          reportEvent(Events.user_login_report_failed, {
            source: 'sawa',
            error_code: `获取facebook token失败`,
            error_content: '获取facebook token失败',
            is_sawaapp_authorize: false,
            h5_type: 'facebook',
            type: '登录',
            h5_scene: 'get_token',
          })
          return
        }
        const { userID: fb_uid, accessToken: fb_token } = response.authResponse
        this.doLoginFacebook(fb_uid, fb_token)
      })
    },
    showCreateModal(cb: any, source: string) {
      if (isDiwania.value) {
        showToast(_T('无已关联的Diwania账号，您可返回至登录主界面注册新账号'))
        return
      }
      showModal(CreateModal, { cb, source })
    },
    async doLoginApple(apple_uid: string, apple_token: string, auto_create = false) {
      if (isTargetVersion()) {
        callbackToClient({
          login_type: 'apple',
          user_id: apple_uid,
          access_token: apple_token,
        })
        return
      }
      this.loading = true
      const [success, accountResp] = await loginWithApple(
        apple_uid,
        apple_token,
        undefined,
        auto_create
      )
      this.loading = false

      if (!success || accountResp.result !== 0) {
        reportEvent(Events.user_login_report_failed, {
          source: 'sawa',
          error_code: `${accountResp.result}`,
          error_content: `${accountResp.message}`,
          is_sawaapp_authorize: false,
          h5_type: 'apple',
          type: '登录',
          h5_scene: 'login',
        })
      }

      if (!success) {
        showToast(_T('获取数据失败，请稍后重试'))
        this.loading = false
        return
      }

      if (accountResp.result !== 0) {
        this.loading = false
        if (accountResp.result === 20001) {
          showBanToast(accountResp.message || '')
          return
        }
        if (accountResp.result === 20193) {
          unregisterRemind(accountResp)
          return
        }

        if (accountResp.result === 20312) {
          this.showCreateModal(() => this.doLoginApple(apple_uid, apple_token, true), 'apple')
        } else {
          showToast(accountResp.toast || _T('获取数据失败，请稍后重试'))
        }
        // todo 创建账号
        // this.showDialog(accountResp, apple_uid, apple_token, 'apple')
        return
      }

      refreshAccount(accountResp, 'sawaksa_apple')
    },
    async doLoginFacebook(fb_uid: string, fb_token: string, auto_create = false) {
      if (isTargetVersion()) {
        callbackToClient({
          login_type: 'facebook',
          user_id: fb_uid,
          access_token: fb_token,
        })
        return
      }
      if (this.loading) {
        return
      }
      this.loading = true
      const [success, accountResp] = await loginWithFacebook(fb_uid, fb_token, auto_create)
      this.loading = false

      if (!success || accountResp.result !== 0) {
        reportEvent(Events.user_login_report_failed, {
          source: 'sawa',
          error_code: `${accountResp.result}`,
          error_content: `${accountResp.message}`,
          is_sawaapp_authorize: false,
          h5_type: 'facebook',
          type: '登录',
          h5_scene: 'login',
        })
      }

      if (!success) {
        showToast(_T('获取数据失败，请稍后重试'))
        this.loading = false
        return
      }

      if (accountResp.result !== 0) {
        this.loading = false
        if (accountResp.result === 20001) {
          showBanToast(accountResp.message || '')
          return
        }
        if (accountResp.result === 20193) {
          unregisterRemind(accountResp)
          return
        }

        if (accountResp.result === 20312) {
          this.showCreateModal(() => this.doLoginFacebook(fb_uid, fb_token, true), 'facebook')
        } else {
          showToast(accountResp.toast || _T('获取数据失败，请稍后重试'))
        }
        // todo 错误弹窗
        // this.showDialog(accountResp, fb_uid, fb_token, 'fb')
        return
      }

      console.log('facebook login rsp', accountResp, success)
      refreshAccount(accountResp, 'sawaksa_fb')
    },
    async doLoginGoogle(token: any, auto_create = false, is_token = true) {
      if (isTargetVersion()) {
        if (!is_token) {
          console.log('callbackToClient', token)
          callbackToClient({
            login_type: 'google',
            access_token: token,
            id_token: '_',
          })
        } else {
          callbackToClient({
            login_type: 'google',
            id_token: token,
          })
        }
        return
      }
      this.loading = true
      const [success, accountResp] = await loginWithGoogle(token, auto_create)
      this.loading = false

      console.log('google login data', success, accountResp)

      if (!success || accountResp.result !== 0) {
        reportEvent(Events.user_login_report_failed, {
          source: 'sawa',
          error_code: `${accountResp.result}`,
          error_content: `${accountResp.message}`,
          is_sawaapp_authorize: false,
          h5_type: 'google',
          type: '登录',
          h5_scene: 'login',
        })
      }

      if (!success) {
        showToast(_T('获取数据失败，请稍后重试'))
        this.loading = false
        return
      }

      if (accountResp.result !== 0) {
        this.loading = false
        if (accountResp.result === 20001) {
          showBanToast(accountResp.message || '')
          return
        }
        if (accountResp.result === 20193) {
          unregisterRemind(accountResp)
          return
        }

        if (accountResp.result === 20312) {
          this.showCreateModal(() => this.doLoginGoogle(token, true), 'google')
        } else {
          showToast(accountResp.toast || _T('获取数据失败，请稍后重试'))
        }

        // todo 错误弹窗
        // this.showDialog(accountResp, fb_uid, fb_token, 'fb')
        return
      }

      console.log('google login rsp', accountResp, success)

      refreshAccount(accountResp, 'sawaksa_google')
    },
    attachSignin() {
      const { google } = window as any

      if (google) {
        if (!this.use_new_google) {
          google.accounts.id.initialize({
            client_id: CLIENT_ID,
            callback: (data: any) => {
              this.doLoginGoogle(data?.credential)
            },
          })
          google.accounts.id.renderButton(document.getElementById('google-login-btn'), {
            theme: 'outline',
            size: 'large',
            type: 'standard',
            width: '311',
            click_listener: async () => {
              reportEvent(Events.h5_loginpage_elementclick, {
                element_name: 'google',
              })
              console.log('google login click 111')
              // sleepBlock(400)
              await sleep(400)
              console.log('google login click 222')
            },
          })
        } else {
          this.codeClient = google.accounts.oauth2.initCodeClient({
            client_id: CLIENT_ID,
            scope: 'openid email profile',
            ux_mode: 'popup',
            callback: (data: any) => {
              console.log('google login data', data)
              this.doLoginGoogle(data?.credential || data?.code, false, false)
            },
          })
          ;(window as any).codeClient = this.codeClient
        }
      }
    },
  },
})
