export function initWebSa(serv_url: string) {
  const sa_sdk_js = './sensorsdata.min.v1.js'
  if (getArgsFromUrl('is_debug')) {
    initWebSensors(SensorSDKName.TEST, false, 'beacon', serv_url, sa_sdk_js)
  }
  else {
    initWebSensors(SensorSDKName.KSA, true, 'beacon', serv_url, sa_sdk_js)
  }
}

export function reportWebSa(event: string, jsonObj: any = {}) {
  const defaults = {
    sawa_device: getArgsFromUrl('uuid') || '',
    $app_id: getArgsFromUrl('bundleid') || '',
    $app_version: getArgsFromUrl('version_name') || '',
  }
  const params = Object.assign(defaults, jsonObj)
  console.log('[reportSa]', event, params)
  reportWebSensors(event, params)
}

export function reportSa(event: string, jsonObj: any = {}) {
  console.log('reportNative>>', event, jsonObj)
  const v = Object.keys(jsonObj)
    .map((k) => `${k}=${encodeURIComponent(jsonObj[k])}`)
    .join('&')
  const schema = isBalootToLudo ? 'sawaludo' : 'sawaksa'
  window.location.href = `${schema}://${event}/?${v}`
}

export function reportLoginClick(source: string) {
  if (isBalootToLudo) {
    report('h5_loginpage_elementclick', { element_name: source })
    return new Promise((r) => setTimeout(r, 300))
  }
  reportSa('baloot_login_click', { source })
  return new Promise((r) => setTimeout(r, 300))
}

export function reportLoginPage() {
  if (isBalootToLudo) {
    report('h5_loginpage', {})
    return new Promise((r) => setTimeout(r, 300))
  }
  return new Promise((r) => setTimeout(r, 0))
}

export function reportLoginFail(source: string, reason: string) {
  reportSa('baloot_login_fail', { source, reason })
}

export function reportRevokeShow(baloot_uid: string) {
  reportWebSa('h5_loginpage_baloot_cancellation_pop_show', {
    type: 'baloot授权登录',
    Baloot_uid: baloot_uid
  })
}

export function reportRevokeClick(element_name: string, baloot_uid: string) {
  reportWebSa('h5_loginpage_baloot_cancellation_pop_click', {
    type: 'baloot授权登录',
    element_name,
    Baloot_uid: baloot_uid
  })
}

export function report(eventName: string, fields = {}) {
  const json = fields
  const scheme = isBalootToLudo ? 'sawaludo' : 'sawaksa'
  window.location.href = `${scheme}://sawaksa_login_event/?eventName=${eventName}&jsonData=${btoa(encodeURIComponent(JSON.stringify(json)))}`
}
