// import moment from 'moment'

/// sync serv ts
let tsDelta: number | null = null

export function getSysTs(): number {
  const cliTs = Math.floor(Date.now() / 1000)
  if (tsDelta === null) {
    // console.warn('Utils.getSysTs but tsDelta is null')
  }
  return (cliTs + (tsDelta == null ? 0 : tsDelta)) * 1000
}

export function updateTsDelta(sysTs: number): void {
  const cliTs = Math.floor(Date.now() / 1000)
  tsDelta = sysTs - cliTs
}

// to:: YYYY/MM/DD HH:MM:SS
export const getCountDownHHMMSS = (to: string | number, nowTs: number) => {
  let rest = 0
  if (typeof to === 'string') {
    const [YMD, HMS] = to.split(' ')
    const toYear = YMD.split('/')[0]
    const toMonth = YMD.split('/')[1]
    const toDay = YMD.split('/')[2]
    const toHour = HMS.split(':')[0]
    const toMin = HMS.split(':')[1]
    const toSec = HMS.split(':')[2]
    const endtime = new Date(`${toYear}/${toMonth}/${toDay} ${toHour}:${toMin}:${toSec}`)
    rest = +endtime - nowTs
  } else if (typeof to === 'number') {
    rest = to - nowTs
  }
  if (rest > 0) {
    const dd: any = Math.floor(rest / (1000 * 3600 * 24))
    const restHH = rest - dd * (1000 * 3600 * 24)
    let hh: any = Math.floor(restHH / (1000 * 3600))
    const restMM = rest - dd * (1000 * 3600 * 24) - hh * (1000 * 3600)
    let mm: any = Math.floor(restMM / (1000 * 60))
    const restSS = rest - dd * (1000 * 3600 * 24) - hh * (1000 * 3600) - mm * (1000 * 60)
    let ss: any = Math.floor(restSS / 1000)
    // dd = dd < 10 ? `0${dd}` : `${dd}`
    hh = dd * 24 + hh
    hh = hh < 10 ? `0${hh}` : `${hh}`
    mm = mm < 10 ? `0${mm}` : `${mm}`
    ss = ss < 10 ? `0${ss}` : `${ss}`
    return [hh, mm, ss]
  }
  return ['00', '00', '00']
}

export const isOverDate = (end: any) => {
  const now = getSysTs()
  return Math.round(now / 1000) > end
}

export const getCurDay = () => {
  const now = Math.round(getSysTs() / 1000)
  // return moment.unix(now).format('YYYYMMDD')
}
