export const fetchUserInfoAsync = () => {
  let callback: any
  let done = false
  const p = new Promise((resolve) => {
    callback = (userInfo: unknown) => {
      if (!done) {
        resolve(userInfo)
      }
      setTimeout(() => {
        resolve({ open_id: '' })
        done = true
      }, 1000)
    }
  })
  if ((window as any).kinoJsApi) {
    ;(window as any).kinoJsApi.getUserInfo(callback)
  } else {
    callback({})
  }
  return p
}

const initPay = () => {
  try {
    ;(window as any).kinoJsApi.initPay && (window as any).kinoJsApi.initPay(true)
  } catch (e) {
    //
  }
}

const isAudienceClient = () => {
  // return true
  if (!(window as any).kinoJsApi) {
    return true
  }
  return !!(window as any).kinoJsApi.initPay
}

const userLogin = () => {
  let callback: any
  const p = new Promise((resolve) => {
    callback = resolve
  })
  if ((window as any).kinoJsApi) {
    ;(window as any).kinoJsApi.userLogin(callback)
  } else {
    callback({})
  }
  return p
}

export const toLiveRoom = (roomId: any) => {
  if (!(window as any).kinoJsApi) {
    return
  }
  if (!roomId) {
    return
  }
  const from = getArgsFromUrl('from')
  if (from === 'streamer') {
    return
  }
  ;(window as any).kinoJsApi.toLiveRoom({
    roomId,
    pop: false,
    from: 'web',
  })
}

export const reportSa = (eventName: string, eventDict: any = {}) => {
  if (!(window as any).kinoJsApi || !(window as any).kinoJsApi.reportSa) {
    return
  }
  ;(window as any).kinoJsApi.reportSa(eventName, {
    ops: eventDict,
  })
}

export const goToUser = (item: any) => {
  if ((window as any).JavaBridgeObj && item && (item.id || item.uid)) {
    ;(window as any).JavaBridgeObj.launchUserPage(parseInt(item.id || item.uid, 10))
  }
}

// 判断js bridge callback 是否支持
export const isSupport = () => {
  let sdk_support_flag = false
  return async (): Promise<boolean> => {
    if (!sdk_support_flag) {
      // 是否为app环境
      const isInApp = await awaitKinoJsApi()
      if (isInApp) {
        const run = (): Promise<boolean> => {
          let callback: any
          let done = false
          const p: Promise<boolean> = new Promise((resolve) => {
            callback = () => {
              if (!done) {
                // Android 5以下不支持
                sdk_support_flag = true
                resolve(true)
              }
              setTimeout(() => {
                resolve(false)
                done = true
              }, 500)
            }
          })
          if ((window as any).kinoJsApi && (window as any).kinoJsApi.getUserInfo) {
            ;(window as any).kinoJsApi.getUserInfo(callback)
          } else {
            callback({})
          }
          return p
        }
        return run()
      }
      return false
    }
    return true
  }
}

export const setRefreshEnable = (b = true) => {
  if ((window as any).kinoJsApi && (window as any).kinoJsApi.setRefreshEnable) {
    ;(window as any).kinoJsApi.setRefreshEnable(b)
  }
  if ((window as any).JavaBridgeObj && (window as any).JavaBridgeObj.enableRefresh) {
    ;(window as any).JavaBridgeObj.enableRefresh(b)
  }
}

export const jumpUserDialog = (uid: string) => {
  if (!(window as any).kinoJsApi || !(window as any).kinoJsApi.router) {
    return
  }
  if (!uid) {
    return
  }
  const link = `zvod://badambiz/userDialog/?uid=uid_${uid}&from=h5live-club-vs`
  ;(window as any).kinoJsApi.router({ action: link })
}

