Vue.component('LoadingComp', Loading)

async function setTime() {
  const time = await getServTs(appType.sawa)
  updateTsDelta(time)
}

function init() {
  return Promise.all([setTime()])
}

function createGoogleSDKScript() {
  const loadGoogleApi = () => {
    store.state.googleLoad = true
  }
  const script = document.createElement('script')
  script.type = 'text/javascript'
  script.src = 'https://accounts.google.com/gsi/client'
  script.onload = loadGoogleApi
  document.body.appendChild(script)
}

export const initLudo = () =>
  exec(async () => {
    createGoogleSDKScript()

    if (((getArgsFromUrlHash('lang') || '') as string).toLowerCase().includes('zh')) {
      setLang({}, '', true)
    } else {
      setLang(lang)
    }
    store.state.data = decodeURIComponent(getArgsFromUrlHash('data')) || ''
    initAppleLogin()
    calculateWindow4()
    // initDarkMode()
    banScale()
    // 神策依赖，生产才打开
    initDomainDial()

    try {
      init()
      DayJs.setOffset(TimeType.Ksa)
      reportSensors('act_meaningful_content_loaded', true)
    } catch (err) {
      console.error(err)
    }

    new Vue({
      store,
      router,
      render: (h) => h(Main),
    }).$mount('#app')
  })

function banScale() {
  document.documentElement.addEventListener(
    'touchstart',
    (event) => {
      if (event.touches.length > 1) {
        event.preventDefault()
      }
    },
    false
  )

  let lastTouchEnd = 0
  document.documentElement.addEventListener(
    'touchend',
    (event) => {
      const now = Date.now()
      if (now - lastTouchEnd <= 300) {
        event.preventDefault()
      }
      lastTouchEnd = now
    },
    false
  )
  document.addEventListener('gesturestart', (event) => {
    event.preventDefault()
  })
}
