// import { setRefreshEnable } from '@badam/common-utils'
import Vue, { Component } from 'vue'

const lockScreen = async (prod = 'sawa') => {
  const obj = (window as any).JavaBridgeObj
  if (obj && obj.enableRefresh) {
    obj.enableRefresh(false)
  }
  const scrollEle = document.querySelector<HTMLElement>('.vscroll')! || document.scrollingElement
  if (!scrollEle) {
    return
  }
  const scrollTop = +scrollEle.scrollTop
  scrollEle.style.cssText += `position:fixed;width:100%;top:-${scrollTop}px`
}

const unlockScreen = async (prod = 'sawa') => {
  const obj = (window as any).JavaBridgeObj
  if (obj && obj.enableRefresh) {
    obj.enableRefresh(true)
  }
  const scrollEle = document.querySelector<HTMLElement>('.vscroll')! || document.scrollingElement
  if (!scrollEle) {
    return
  }
  const scrollTop = parseInt(scrollEle.style.top, 10) || scrollEle.scrollTop
  scrollEle.style.cssText += 'position:relative;width:100%;top:0'
  scrollEle.scrollTop = Math.abs(scrollTop)
  // console.log(scrollEle.scrollTop, scrollTop)
}

const Modal = Vue.extend({
  data() {
    return {
      show: false,
      curComp: null as unknown as Component,
      params: {} as any,
      className: '',
      isTop: false,
      cancelable: true,
      closeCb: () => {},
      openTimeTs: 0,
    }
  },
  mounted() {
    // setRefreshEnable(false)
  },
  methods: {
    onPressMask() {
      if (this.cancelable) {
        const now = Date.now()
        const diff = now - this.openTimeTs
        if (diff > 2500) {
          this.onCancel()
        } else {
          setTimeout(() => {
            this.onCancel()
          }, 2500 - diff)
        }
      }
    },
    onCancel() {
      this.show = false
    },
    showRefreshControllIfNeed: (isTop: any) => {
      if ((window as any).kinoJsApi && (window as any).kinoJsApi.setRefreshEnable) {
        ;(window as any).kinoJsApi.setRefreshEnable(isTop)
      }
      if ((window as any).JavaBridgeObj && (window as any).JavaBridgeObj.enableRefresh) {
        ;(window as any).JavaBridgeObj.enableRefresh(isTop)
      }
    },
  },
  watch: {
    show(n) {
      console.log('show modal', n)
      if (n) {
        lockScreen()
      } else {
        unlockScreen()
        this.closeCb()
      }
    },
  },
})

const showModal = (
  component: Component,
  params: any,
  closeCb: () => void = () => {},
  cancelable = true
): any => {
  const modal = new Modal({ el: document.createElement('div') })
  const el = modal.$el
  modal.curComp = component
  modal.params = params
  modal.cancelable = cancelable
  modal.className = params.class || ''
  document.body.appendChild(el)
  modal.show = true
  modal.closeCb = closeCb
  modal.openTimeTs = Date.now()

  return modal
}

export { showModal }

export default Modal
