/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */
import Vue from 'vue'
import { addEvent } from '@@/api'
import { getArgsFromUrl, getOpenid, getSessionKey } from './func'

export const wrapEvent = async (eventName: string, eventDict: EventDict): Promise<BadamEvent> => ({
  eventName,
  uuid: '-1',
  ua: navigator.userAgent,
  op: (await getOpenid()) || null,
  tk: getSessionKey() || null,
  href: location.href,
  uid: null,
  rid: getArgsFromUrl('room_id') || null,
  version: process.env.VUE_APP_VERSION,
  proj: process.env.VUE_APP_PROJ,
  dict: eventDict,
})

export const reportEvent = async (eventName: string, eventDict: EventDict) => {
  const r = await wrapEvent(eventName, eventDict)
  addEvent(r)
}

export const reportError = (eventName: string, err: Error, vm: string, info: string) => {
  const stack = err.stack?.split('\n').slice(0, 6).join('\n')
  const dict: EventDict = {
    str_param2: JSON.stringify({
      vm,
      info,
    }),
    str_param3: stack,
  }
  reportEvent(eventName, dict)
}

export const initErrorHandler = () => {
  Vue.config.errorHandler = function onError(err, vm, info) {
    reportError('web_err', err, vm.$el.id || vm.$el.className, info)
  }

  // message：错误信息（字符串）。
  // source：发生错误的脚本URL（字符串）
  // lineno：发生错误的行号（数字）
  // colno：发生错误的列号（数字）
  // error：Error对象（对象）
  window.onerror = function onerror(message, source, lineno, colno, error) {
    reportError(
      'web_err',
      { message: message.toString(), name: error?.name || '', stack: error?.stack || '' },
      '',
      'window.onerror'
    )
  }

  window.addEventListener(
    'error',
    (error) => {
      reportError(
        'web_err',
        {
          message: error.message || '',
          name: error.filename || '',
          stack: error.message || '',
        },
        '',
        'error'
      )
    },
    true
  )

  window.addEventListener('unhandledrejection', (e) => {
    // e.preventDefault()
    reportError(
      'web_err',
      { message: e.reason.message || '', name: e.reason.name || '', stack: e.reason.stack || '' },
      '',
      'unhandledrejection'
    )
  })
}
