import { Component } from 'vue-property-decorator'
import Vue from 'vue'
import styles from '@@/assets/ar/css/app.module.scss'
import loadingDirective from '@@/components/common/loading/directive'
import { registryToast } from '@@/components/ui/toast.vue'
import Login from '@@/components/login'
import Login2 from '@@/components/login/Login2'
import { getArgsFromUrl } from '@badam/common-utils'
import BaseComponent from './components/base'
import { LangMixin, _T } from './utils/lang'
import bridge, { BridgeMixin, isLive, setRefreshEnable } from './utils/bridge'
import { throttle, convertTsx } from './utils/func'
import store from './store/index'
import conf from './conf'
import { getServTs } from './api'
import { updateTsDelta } from './utils/time'
import '@@/assets/ar/css/login.scss'

Vue.mixin(LangMixin)
Vue.mixin(BridgeMixin)
Vue.mixin({
  computed: {
    ready(): boolean {
      return store.state.ready
    },
  },
})
Vue.directive('loading', loadingDirective as any)
registryToast()

interface LoginProps {}

const Login1Comp = convertTsx<LoginProps>(Login)
const Login2Comp = convertTsx<LoginProps>(Login2)
const LoginComp = getArgsFromUrl('user_mode') === '1' ? Login2Comp : Login1Comp
console.warn('>>>>', getArgsFromUrl('user_mode'), getArgsFromUrl('user_mode') === '1')

@Component({})
export default class App extends BaseComponent {
  created() {
    bridge.initSawaSDK()
  }

  onClickApp() {
    // console.warn('onClick app')
    // JavaBridgeObj.setDebug(true)
    // JavaBridgeObj.router('sawa://friend_square')
  }

  onScroll = throttle(() => {
    if (!this.$refs || !this.$refs.scroller) {
      return
    }
    const { scrollTop } = this.$refs.scroller as any
    const isTop = scrollTop < 10
    this.showRefreshControllIfNeed(isTop)
  }, 30)

  showRefreshControllIfNeed = (isTop: any) => {
    setRefreshEnable(isTop)
  }

  async syncServTsIfNeed() {
    if (conf.clientTs) {
      return
    }
    if (isLive()) {
      const servTs = (await getServTs()) as any
      updateTsDelta(servTs)
    }
  }

  render(): any {
    return (
      <div
        id={styles.app}
        ref="scroller"
        onScroll={this.onScroll}
        // onClick={this.onClickApp}
      >
        <LoginComp />
      </div>
    )
  }
}
