import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

interface StoreState {
  ready: boolean
  actStartTs: number
  actEndTs: number
}

export default new Vuex.Store({
  state: {
    ready: false,
    actStartTs: +new Date() - 7 * 24 * 3600 * 1000,
    actEndTs: +new Date() + 7 * 24 * 3600 * 1000,
  },
  actions: {},
  modules: {},
  mutations: {
    setReady(state: StoreState, ready: boolean) {
      state.ready = ready
    },
    setActInfo(state: StoreState, info: any) {
      const { actStartTs, actEndTs } = info
      state.actStartTs = actStartTs
      state.actEndTs = actEndTs
    },
  },
})
